.box{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  align-items: center;
}

.container{
  border: 2px solid black;
  width: 800px;
  height: 150px;
  text-align: center;
}

@media screen and (max-width:575px) and (min-width:475px) {
  .container{
    width: 450px;
  }
  #btn{
    width: 450px;
  }
}
@media screen and (max-width:675px) and (min-width:575px) {
  .container{
    width: 550px;
  }
  #btn{
    width: 550px;
  }
}
@media screen and (max-width:475px) and (min-width:375px) {
  .container{
    width: 350px;
  }
  #btn{
    width: 350px;
  }
}
@media screen and (max-width:375px) and (min-width:275px) {
  .container{
    width: 250px;
  }
  #btn{
    width: 250px;
  }
}